import React, { useState } from 'react';
import { toast } from 'react-toastify';
import Loader from './Loader';

function ImageUpload() {
  const [loader, setLoaader] = useState(false);
    
  const [file, setFile] = useState(null);

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };

  const handleSubmit = async (event) => {
    setLoaader(true)
    event.preventDefault();

    if (!file) {
      toast.error('Please select a file');
      return;
    }

    const formData = new FormData();
    formData.append('img', file);

    try {
      const response = await fetch('https://fyp-backend-live.vercel.app/upload', {
        method: 'POST',
        body: formData,
      });

      if (response.ok) {
        toast.success('Image uploaded successfully');
        setLoaader(false)
      }
    } catch (error) {
      toast.error('Error uploading image');
      setLoaader(false)
     
    }
  };

  return (
    <>
    {loader && <Loader />}
    <form onSubmit={handleSubmit} className='text-center mb-3' encType="multipart/form-data">
      <input type="file" style={{cursor: "pointer"}} name="img" onChange={handleFileChange} />
      <button className=' btn btn-primary bc' type="submit">Upload</button>
    </form>
  </>
  );
}

export default ImageUpload;
