import React from 'react'
import { useSelector } from 'react-redux'
import { Link, Outlet, useNavigate } from 'react-router-dom'
import './Navbar.css'
import { toast } from 'react-toastify'
import { useAuth0 } from '@auth0/auth0-react'


function Navbar() {
  const navigate = useNavigate()
  let cart_details = useSelector(state => state.cartReducer.cart)
  let auth = localStorage.getItem('token')

  const { user, isAuthenticated } = useAuth0();

  const { loginWithRedirect } = useAuth0();
  const { logout } = useAuth0();





  
    let fname = null
    let lname = null


  if(auth){
  
  let name = localStorage.getItem('token');
  let nameObject = JSON.parse(name); // Parse the JSON
  // // Extract the name value
  name = nameObject.token;
     fname = name.fname
     lname = name.lname
  
  console.log(fname + lname)
  }

  

  const logoutAuth = () => {
    localStorage.clear()
    navigate("/")
    toast.success("Logged Out Successfully")
  }

  return (
    <>
        <div>
            <div className="container-fluid my-bg fixed-top ">
              <div className="row">





              <div className="col-9">
          <nav className="navbar navbar-expand-lg  navbar-light ">
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>

              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <>

                
                  <ul className="navbar-nav mx-auto position-relat mb-2 mb-lg-0">
                    <li className="nav-item">
                      <Link className="nav-link" aria-current="page" to="/">Home</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/categories">Categories</Link>
                    </li>
                    <li style={{width: "fit-content"}}>
                      <div  className='pe-2'>
                        <div className="position-relative">
                          <Link className="nav-link me-2 pe-0" to="/shopping" tabIndex="-1" aria-disabled="true">Shopping Cart</Link>
                          <span className="position-absolute top-0 start-100 badge bg-dark">
                            {cart_details.length}
                          </span>
                        </div>
                      </div>
                    </li>

<li>
          <div className="">

          {isAuthenticated || auth? (<>
              {isAuthenticated && (<>
                <button onClick={() => logout({ logoutParams: { returnTo: window.location.origin } })}
                className="nav-link2" >
                  Log Out with Auth0
                </button>
                </>)} 
                {auth && (<>
                  <button className="nav-link2" onClick={logoutAuth} tabIndex="-1" aria-disabled="true">
                    Log out with JWT</button>
                </>)} 
                
         
         
              </>
              ) :

                (<>
                  {/* <li className="nav-item"> */}
                  <div onClick={() => loginWithRedirect()} className=" nav-link2  me-2">Log In with Auth0</div>
                  <Link className="nav-link2 " to="/user" tabIndex="-1" aria-disabled="true">Register / Login with JWT</Link>
                  {/* </li> */}

                </>
                )}

           
                   </div></li>


                                     </ul>


                 

                </>
              </div>
          </nav>
          </div>



          
            <div className="navbar-nav  d-flex justify-content-start align-items-start  col-3 mb-lg-0">

           
            {isAuthenticated || auth? (<>
                {/* <li className="nav-item"> */}
                <h3 className="nav-link m-0 ps-2" aria-current="page" to="/">welcome<br />
                {isAuthenticated && (<>{user.name}</>)} 
                {auth && (<>{fname+ " "  +  lname}</>)}</h3>
                {/* </li> */}
              </>) : null}


              </div>




            </div>
            </div>
        </div>
      </>
  )
}

export default Navbar
